import { Tabbar, TabItem } from 'mint-ui';

export default {
	name: 'tabbar',
	data(){
		return {
			navTab:this.href,
			vuegConfig:{
				disable: true
			}
		}
	},
	watch:{
        navTab(link){
			this.$router.replace(link);
			//this.navTab = link;
			console.info(link)
			console.log(this.href)
        },
        '$route'(){
            this.navTab = this.$route.path;
        }
	},
	components:{
		Tabbar, TabItem
	},
	mounted(){
		// alert(this.navTab)
		// console.log($route.path)
	},
	props:["href"]
}



